.profile {
    width: 1000px;
    background-color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    width: 370px;
    box-shadow: 0 0 20px #000000ed;
    border-radius: 10px;
  }
  
  .description {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: inherit;
    border-bottom: #ffff00 solid 2px;
  }
  
  .avatar {
    width: 200px;
    box-shadow: 0 0 3px 0px #ffee01;
    border-radius: 50%;
    background-color: #ffff00a2;
    padding: 20px 10px 0px 10px;
    margin-bottom: 10px;
    margin-top: 20px;
  }
  
  .name {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 10px;
    color: #ffff00;
    margin-top: 10px;
  }
  
  .tag {
    font-weight: 500;
    font-size: 20px;
    color: #ffff0075;
    margin-bottom: 10px;
    margin-top: 5px;
  }
  
  .location {
    font-weight: 700;
    font-size: 20px;
    color: #ffff00a2;
    margin-bottom: 15px;
    margin-top: 5px;
  }
  .stats {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: auto;
    padding: 0;
    width: inherit;
    background-color: #00000065;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  
  .stats > li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5px;
    min-width: 80px;
    padding: 7px 0;
  }
  
  .label {
    margin-right: 3px;
    font-weight: 700;
    font-size: 20px;
    color: #ffff00;
    margin-bottom: 5px;
  }
  
  .quantity {
    font-weight: 500;
    font-size: 20px;
    color: #ffff00a2;
  }